import React from 'react';

import {
  responsiveNavbarWidth,
  responsiveNavbarHorizontalPadding,
  responsiveNavbarVerticalPadding,
  responsiveNavbarPosition,
  responsiveNavbarFlexJustify,
  responsiveNavbarContainerFlexJustify,
} from './navbar.responsive_styles';

import ColorModeToggler from '../../togglers/ColorModeToggler';
import NavLink from '../../links/nav-link/navlink.component';
import { navLinks } from '../../../data/links/nav-links.data';
import { MdMenu, MdClose } from 'react-icons/md';
import {
  Box,
  Flex,
  HStack,
  IconButton,
  Button,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuDivider,
  useDisclosure,
  useColorModeValue,
  Stack,
} from '@chakra-ui/react';

// TODO: Add colorModeToggler to end of navbar
// TODO: Mobile responsivesness (iphone 12/13)
const Navbar = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  return (
    <Box  as='nav'
          px={responsiveNavbarHorizontalPadding}
          py={responsiveNavbarVerticalPadding}
          bg='black'
          position={responsiveNavbarPosition}
          zIndex={50}
          w='full'
          >
      <Flex justify='center'
            align='center'
            bg={useColorModeValue('black', 'gray.900')} 
            py='16px'
            // border='1px'
            >
        <Flex w='full'
              justify={responsiveNavbarContainerFlexJustify}
              // border='1px'
              >
          <IconButton size='lg'
                      aria-label='Open Menu'
                      display={{ md: 'none' }}
                      onClick={isOpen ? onClose : onOpen}
                      >
            <Flex justify='center' 
                  align='center'
                  >
            {isOpen ? <MdClose fontSize='24px'/> : <MdMenu fontSize='16px'/>}
            </Flex>
          </IconButton>
          
          <Flex as='nav'
                display={{ base: 'none', md: 'flex' }}
                // border='1px' borderColor='white'
                w={responsiveNavbarWidth}
                justify={responsiveNavbarFlexJustify}
                >
              {
                navLinks.map(
                  (link) => <NavLink key={link.id} 
                                     link={link} 
                                     paddingLR='24px'/>
                )
              }
            </Flex>
            
          {/* <Flex align='center'>
            <Menu closeOnBlur={true} isLazy={true}>
              <MenuButton
                as={Button}
                rounded='sm'
                variant='link'
                cursor='pointer'
                minW={0}>
              </MenuButton>
              <MenuList>
                <MenuItem>Link 1</MenuItem>
                <MenuItem>Link 2</MenuItem>
                <MenuDivider />
                <MenuItem>Link 3</MenuItem>
              </MenuList>
            </Menu>
          </Flex> */}
        </Flex>
        <ColorModeToggler alignSelf='end'/>

        {/* // Mobile Popout menu
        {isOpen ? (
          <Box display={{ md: 'none' }} zIndex={10}>
            <Flex as='nav'
                  direction='column'
                  justify='space-between'>
              {navLinks.map((link) => (
                <Box py='8px' fontSize='28px'>
                  <NavLink key={link.id} link={link} />
                </Box>
              ))}
            </Flex>
          </Box>
        ) : null} */}
      </Flex>
    </Box>
  );
}

export default Navbar;
